import React, { Component } from "react";
import "../../../App.css";

class ClaimsModal extends Component {
  render() {
    return (
      <div>
        {/* Create Claim Dialog  */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="createClaimDialog"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content CreateAmendment-modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="text-white text-CreateAmendment">
                    <p>Create New Seed Claim</p>
                  </div>
                  <div class="CreateAmendmentForm">
                    <form action="">
                      <div class="container">
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Internal Claim#"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Lot #"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Species"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Variety"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Color"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Class"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-12">
                            <textarea
                              placeholder="Description of issue"
                              name=""
                              class="form-control"
                              id=""
                              cols="30"
                              rows="3"
                            ></textarea>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>

                  <div class="createClaimDialog-button">
                    <a href="" class="createClaimDialog-btn btn-bg-ash">
                      Add Photos
                    </a>
                    <a href="" class="createClaimDialog-btn">
                      Cancel
                    </a>
                    <a
                      href=""
                      data-toggle="modal"
                      data-target="#createClaimDialog2"
                      data-dismiss="modal"
                      class="createClaimDialog-btn"
                    >
                      Save
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Create Claim dialog 2 */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="createClaimDialog2"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content seeding-dialog23-modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="text-white">
                    <p>E-mail Seed Claim</p>
                  </div>
                  <br />
                  <div class="modal-form-area">
                    <div class="seeding-checkbox">
                      <div class="form-check form-check-inline">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          id="inlineCheckbox1"
                          value="option1"
                          checked
                        />
                        <label
                          class="form-check-label text-white"
                          for="inlineCheckbox1"
                        >
                          Add Vendor
                        </label>
                      </div>
                    </div>
                    <form action="">
                      <div class="form-group">
                        <input
                          type="text"
                          class="form-control"
                          placeholder="dl@boobsmarket.com, joe@vendor.com"
                        />
                      </div>
                    </form>
                  </div>

                  <div class="seeding-button-area">
                    <a href="/#" class="seeding-dialog-btn">
                      Cancel
                    </a>
                    <a href="claims.html" class="seeding-dialog-btn">
                      Email
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default ClaimsModal;
