import React, { Component } from "react";

class SearchOrderNotificatoins extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="plusNotificationBar">
          <i onClick={this.OnShowClick} class="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <a href="main-dashboard.html">
                          <i className="fa fa-home"></i>
                        </a>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div className="row mt-2 mb-2">
                <div className="col-sm-12">
                  <ul className="to-Purchasing-action-menu-1-list">
                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-left fa-2x"></i>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">3</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">2</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>

                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-right fa-2x"></i>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="row mt-3">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a
                      href="#"
                      data-toggle="modal"
                      data-target="#diallogPo7"
                      data-dismiss="modal"
                    >
                      Create <br /> View
                    </a>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a href="#">
                      Saved <br /> View
                    </a>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box to-hide">
                    <a href="#">
                      Seeding Thing <br /> Four
                    </a>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box to-hide">
                    <a href="#">
                      Seeding Thing <br /> Five
                    </a>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box to-hide">
                    <a href="#">
                      Seeding Thing <br /> Seven
                    </a>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box to-hide">
                    <a href="#">
                      Seeding Thing <br /> Eight
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Dialog - PO – 7 modal --> */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="diallogPo7"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="modal-text-content diallogPo7Content">
                    <p>Save View As</p>
                  </div>
                  <div class="diallogPo7-form">
                    <form action="">
                      <div class="form-group">
                        <input
                          type="text"
                          class="form-control"
                          placeholder="Gerg's Custom Purchase View"
                        />
                      </div>
                      <div class="form-group">
                        <input
                          type="text"
                          class="form-control"
                          placeholder="This is descriptions of the view"
                        />
                      </div>
                      <div class="form-check">
                        <input
                          type="checkbox"
                          class="form-check-input"
                          id="exampleCheck1"
                        />
                        <label
                          class="form-check-label diallogPo7Checkbox"
                          for="exampleCheck1"
                        >
                          Save as private
                        </label>
                      </div>
                      <div class="form-group diallogPo7Button">
                        <a
                          href="Purchasing-Search-1.html"
                          class="btn btn-success"
                        >
                          Cancel
                        </a>
                        <a
                          href=""
                          data-toggle="modal"
                          data-target="#diallogPo8"
                          data-dismiss="modal"
                          class="btn btn-success"
                        >
                          Save
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - PO – 8 modal --> */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="diallogPo8"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="modal-text-content text-center forgetPinModelContent">
                    <p>
                      "Greg's Custom Perchase View"
                      <br /> has been successfully added
                    </p>
                  </div>
                  <div class="text-center forgetPinSubmit">
                    <a href="Purchasing-Search-1.html">Okay</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
export default SearchOrderNotificatoins;
