import React, { Component } from "react";

class SearchPurchaseOrderHeader extends Component {
  state = {
    showSearchFilter: false,
    HideSearchFilter: true
  };

  OnShowClick = () => {
    this.setState({
      showSearchFilter: !this.state.showSearchFilter,
      HideSearchFilter: !this.state.HideSearchFilter
    });
    // this.setState({ HideSearchFilter: !this.state.HideSearchFilter });
  };
  render() {
    const { showSearchFilter } = this.state;
    const { HideSearchFilter } = this.state;
    return (
      <div>
        <div class="to-filter-bg">
          <div class="row">
            <div class="col-md-4 col-sm-12">
              {HideSearchFilter ? (
                <div class="to-filter-text">
                  Add filters from the search box
                </div>
              ) : null}

              {showSearchFilter ? (
                <div class="to-search-filter-to">
                  <a onClick={this.OnShowClick}>
                    x &nbsp; Search filter example
                  </a>
                </div>
              ) : null}
            </div>
            <div class="col-md-3 offset-md-5  text-right">
              <div class="input-group to-input-tb">
                <input
                  class="form-control py-2"
                  placeholder="Search filter example"
                />
                <span class="input-group-append">
                  <button
                    class="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i
                      onClick={this.OnShowClick}
                      class="fa fa-search to-icon-color"
                    ></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SearchPurchaseOrderHeader;
