import React, { Component } from "react";

class PurchaseDetailsByItem extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid ">
          <form className="es_purchase-input">
            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputTerms">
                  Item Type
                </label>
                <select
                  id="inputTerms"
                  className="form-control to-input-f-b-c to-classic"
                >
                  <option selected>Plastics</option>
                  <option>...</option>
                </select>
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputPurchaseOrder">
                  Item Cost
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputPurchaseOrder"
                  placeholder="$125.00"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputVendor">
                  Item Description
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputVendor"
                  placeholder="Green Plastic thing"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputCreator">
                  # of Units/Loads
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputCreator"
                  placeholder="5"
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputTerms">
                  Shipping Type
                </label>
                <select
                  id="inputTerms"
                  className="form-control to-input-f-b-c to-classic"
                >
                  <option selected>Shipping Type</option>
                  <option>...</option>
                </select>
              </div>

              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputDiscounts">
                  Surcharge
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputDiscounts"
                  placeholder="$10.00"
                />
              </div>
              <div className="form-group col-md-3">
                <label
                  className="to-title-f-p-size"
                  for="inputDescriptionOrder"
                >
                  Ship Date
                </label>
                <input
                  type="date"
                  className="form-control to-input-f-b-c"
                  id="inputDescriptionOrder"
                  placeholder="Order description"
                />
              </div>
              <div className="form-group col-md-3">
                <div className="form-check">
                  <label
                    className="to-title-f-p-size"
                    for="inputDescriptionOrder"
                  >
                    FOB
                  </label>{" "}
                  <br />
                  <input
                    className="form-check-input position-static es_purchase-checkbox to-input-f-b-c"
                    type="checkbox"
                    id="blankCheckbox"
                    value="option1"
                    aria-label="..."
                  />
                </div>
              </div>
            </div>
          </form>
        </div>
        <section className="button-area">
          <div className="container ">
            <div className="row">
              <div className="col-12">
                <div className="text-center">
                  <button
                    onclick="location.href='purchasing-newPurchase-11.html';"
                    type="button"
                    className="btn to-button-default forced-center"
                  >
                    + Add Item
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default PurchaseDetailsByItem;
