import React, { Component } from "react";

class AnotherFilter extends Component {
  render() {
    return (
      <div>
        <div class="to-filter-bg">
          <div class="row">
            <div class="col-lg-4 offset-lg-8  text-right">
              <div class="input-group to-input-tb">
                <input class="form-control py-2" placeholder="Another filter" />
                <span class="input-group-append">
                  <button
                    class="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i class="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AnotherFilter;
