import React, { Component } from "react";
import Header from "../../components/Header";
import ClaimsFilter from "./Components/ClaimsFilter";
import ClaimsTable from "./Components/ClaimsTable";
import CopyRightsText from "../../components/CopyRightsText";
import ClaimsButton from "./Components/ClaimsButton";
import ClaimsModal from "./Components/ClaimsModal";

class Claims extends Component {
  render() {
    return (
      <div>
        <Header pageTitle="Claims" />
        <ClaimsFilter />
        <ClaimsTable />
        <ClaimsButton />
        <ClaimsModal />
        <CopyRightsText />
      </div>
    );
  }
}

export default Claims;
