import React, { Component } from "react";

class SeedDemandMainListHeader extends Component {
  render() {
    return (
      <div>
        <div class="to-filter-bg-2">
          <div class="row">
            <div class="col-lg-4  col-sm-12">
              <div class="to-filter-text">Add filters from the search box</div>
            </div>
            <div class="col-lg-4 offset-lg-4  text-right">
              <div class="input-group to-input-tb">
                <input
                  class="form-control py-2"
                  placeholder="Search filter example"
                />
                <span class="input-group-append">
                  <button
                    class="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i class="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default SeedDemandMainListHeader;
