import React, { Component } from "react";
import "../../../App.css";
class AmendmentsBottomButtons extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container-fluid ">
            <div className="row">
              <div className="col-xl-6">
                <button
                  data-toggle="modal"
                  data-target="#CreateAmendment"
                  type="button"
                  className="btn  btn-lg to-button-default"
                >
                  Add New
                </button>
              </div>

              <div className="col-xl-6">
                <div className="BottomButtons">
                  <button
                    type="button"
                    className="btn  btn-lg to-button-disabled"
                    disabled
                  >
                    Merge
                  </button>
                  <button
                    type="button"
                    className="btn  btn-lg to-button-default"
                  >
                    Save
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section class="logout-modal">
          <div
            class="modal fade"
            id="CreateAmendment"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content CreateAmendment-modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="text-white text-CreateAmendment">
                    <p>Create New Amendment</p>
                  </div>
                  <div class="CreateAmendmentForm">
                    <form action="">
                      <div class="container">
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Internal Claim#"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Lot #"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Species"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Variety"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Color"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Class"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-12">
                            <textarea
                              placeholder="Description of issue"
                              name=""
                              class="form-control"
                              id=""
                              cols="30"
                              rows="3"
                            ></textarea>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>

                  <div class="seeding-button-area seeding-button-CreateAmendment">
                    <a href="" class="seeding-dialog-btn">
                      Cancel
                    </a>
                    <a
                      href=""
                      data-dismiss="modal"
                      data-target="#createClaimDialog2"
                      data-toggle="modal"
                      class="seeding-dialog-btn"
                    >
                      Save
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default AmendmentsBottomButtons;
