import React, { Component } from "react";

class NewPurchaseHeader extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid ">
          <form className="es_purchase-input">
            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputOrderDate">
                  Order Date
                </label>
                <input
                  type="date"
                  className="form-control to-input-f-b-c"
                  id="inputEmail4"
                  placeholder="04/10/2019"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputPurchaseOrder">
                  Purchase Order #
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputPurchaseOrder"
                  placeholder="246813579"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputVendor">
                  Vendor
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputVendor"
                  placeholder="ACME COMPANY"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputCreator">
                  Creator
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputCreator"
                  placeholder="Greg Brewster"
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputSalesOrder">
                  Sales Order #
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputSalesOrder"
                  placeholder="0987654321"
                />
              </div>
              <div className="form-group col-md-3">
                <div className="row">
                  <div className="col">
                    <label className="to-title-f-p-size" for="inputTerms">
                      Terms{" "}
                    </label>

                    <select
                      id="inputTerms"
                      className="form-control to-input-f-b-c to-classic"
                    >
                      <option selected>Net 30</option>
                      <option>...</option>
                    </select>
                  </div>

                  <div className="col">
                    <label className="to-title-f-p-size" for="inputDept">
                      Dept
                    </label>
                    <input
                      type="text"
                      className="form-control to-input-f-b-c"
                      id="inputDept"
                    />
                  </div>
                </div>
              </div>

              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputDiscounts">
                  Discounts
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputDiscounts"
                  placeholder="Discounts"
                />
              </div>
              <div className="form-group col-md-3">
                <label
                  className="to-title-f-p-size"
                  for="inputDescriptionOrder"
                >
                  Description of Order
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputDescriptionOrder"
                  placeholder="Order description"
                />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
export default NewPurchaseHeader;
