import React, { Component } from "react";

class SowSummaryHeader extends Component {
  render() {
    return (
      <div>
        <div class="to-filter-bg">
          <div class="row">
            <div class="form-group col-lg-3">
              <div class="row">
                <div class="col to-select-margin">
                  <div class="to-purchase-title-2">Sow Week</div>

                  <select id="inputTerms" class="form-control to-classic-seed">
                    <option selected>Week 3</option>
                    <option>Week 1</option>
                    <option>Week 2</option>
                    <option>Week 3</option>
                    <option>Week 4</option>
                  </select>
                </div>
              </div>
            </div>

            <div class="col-lg-5 offset-lg-4  text-right">
              <div class="seeding-sow-space">
                <div class="input-group to-input-tb">
                  <input class="form-control py-2" placeholder="Add filter" />
                  <span class="input-group-append">
                    <button
                      class="btn btn-outline-secondary to-filter-btn"
                      type="button"
                    >
                      <i class="fa fa-search to-icon-color"></i>
                    </button>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SowSummaryHeader;
