import React, { Component } from "react";
import "../../../App.css";

class ClaimsButton extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container-fluid ">
            <div className="row">
              <div className="col-xl-4 col-md-4">
                <button
                  data-toggle="modal"
                  data-target="#createClaimDialog"
                  type="button"
                  className="btn  btn-lg to-button-default"
                >
                  Add New
                </button>
              </div>

              <div className="col-xl-6 offset-xl-2 col-md-8">
                <div className="BottomButtons">
                  <button
                    type="button"
                    className="btn  btn-lg to-button-default"
                  >
                    Print/Reprint
                  </button>
                  <button
                    type="button"
                    className="btn  btn-lg to-button-default"
                  >
                    Remove
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default ClaimsButton;
