import React, { Component } from "react";
import "../../../App.css";
class SeedDemandMainListNotificatoins extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="pur-dash-plusNotificationBar">
          <i onClick={this.OnShowClick} class="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <a href="main-dashboard.html">
                          <i className="fa fa-home"></i>
                        </a>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div className="row mt-2 mb-2">
                <div className="col-sm-12">
                  <ul className="to-Purchasing-action-menu-1-list">
                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-left fa-2x"></i>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">3</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">2</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a
                        className="to-seed-icon to-Purchasing-action-menu-1-icon"
                        href="/#"
                      >
                        <img
                          src={require("../../../assets/icons/icon_seeding_main.png")}
                          alt=""
                        />
                        <span className="header-notification">1</span>
                      </a>
                    </li>

                    <li>
                      {" "}
                      <a
                        className="to-Purchasing-action-menu-1-icon arrow"
                        href="/#"
                      >
                        <i className="fa fa-caret-right fa-2x"></i>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="row mt-3">
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a href="/#">Import</a>
                  </div>
                </div>
                <div class="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a href="/#">Edit</a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a
                      href="#"
                      data-toggle="modal"
                      data-target="#CreateAmendment"
                    >
                      Add <br /> Ammendment
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div class="to-Purchasing-action-menu-box">
                    <a
                      href="#"
                      data-toggle="modal"
                      data-target="#"
                      data-dismiss="modal"
                    >
                      Select Order <br /> Template
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12 col-md-6 to-hide">
                  <div class="to-Purchasing-action-menu-box">
                    <a
                      href="#"
                      data-toggle="modal"
                      data-target="#CreateAmendment"
                    >
                      Add <br /> Ammendment
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6 to-hide">
                  <div class="to-Purchasing-action-menu-box">
                    <a
                      href="#"
                      data-toggle="modal"
                      data-target="#"
                      data-dismiss="modal"
                    >
                      Select Order <br /> Template
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Create Amendment --> */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="CreateAmendment"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content CreateAmendment-modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="text-white text-CreateAmendment">
                    <p>Create New Amendment</p>
                  </div>
                  <div class="CreateAmendmentForm">
                    <form action="">
                      <div class="container">
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Internal Claim#"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Lot #"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Species"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Variety"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Color"
                              />
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="form-group">
                              <input
                                type="text"
                                class="form-control"
                                placeholder="Class"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-12">
                            <textarea
                              placeholder="Description of issue"
                              name=""
                              class="form-control"
                              id=""
                              cols="30"
                              rows="3"
                            ></textarea>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>

                  <div class="seeding-button-area seeding-button-CreateAmendment">
                    <button class="seeding-dialog-btn">Cancel</button>
                    <button
                      data-toggle="modal"
                      data-target="#createClaimDialog2"
                      data-dismiss="modal"
                      class="seeding-dialog-btn"
                    >
                      Save
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Create Claim dialog 2 --> */}
        <section class="logout-modal">
          <div
            class="modal fade"
            id="createClaimDialog2"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content seeding-dialog23-modal-content">
                <div class="modal-body">
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div class="text-white">
                    <p>E-mail Seed Claim</p>
                  </div>
                  <br />
                  <div class="modal-form-area">
                    <div class="seeding-checkbox">
                      <div class="form-check form-check-inline">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          id="inlineCheckbox1"
                          value="option1"
                        />
                        <label
                          class="form-check-label text-white"
                          for="inlineCheckbox1"
                        >
                          Add Vendor
                        </label>
                      </div>
                    </div>
                    <form action="">
                      <div class="form-group">
                        <input
                          type="text"
                          class="form-control"
                          placeholder="dl@boobsmarket.com, joe@vendor.com"
                        />
                      </div>
                    </form>
                  </div>

                  <div class="seeding-button-area">
                    <button class="seeding-dialog-btn">Cancel</button>
                    <button class="seeding-dialog-btn">Email</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
export default SeedDemandMainListNotificatoins;
