import React, { Component } from "react";
import "../../../App.css";

class BallWeeklyTableBottomArea extends Component {
  render() {
    return (
      <div>
        <section>
          <div class="container-fluid ">
            <div class="row to-button-area">
              <div class="col-xl-2 ">
                <div class="search-menu-icon">
                  <a href="/#">
                    <img src={require("../../../assets/icons/menu.svg")} />
                  </a>
                  <a href="/#" class="calendar-icon">
                    <i class="fa fa-calendar"></i>
                  </a>
                </div>
              </div>
              <div class="col-xl-2 offset-xl-4">
                <div class="to-custom-text-new2">
                  <p>
                    <span class="already-ordered"></span>Already Ordered
                  </p>
                </div>
              </div>
              <div class="col-xl-2">
                <div class="to-custom-text-new2">
                  <p>
                    <span class="should-ordered"></span>Should Order
                  </p>
                </div>
              </div>
              <div class="col-xl-2">
                <div class="to-custom-text-new2">
                  <p>
                    <span class="not-ordered"></span>Not Ordered
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BallWeeklyTableBottomArea;
