import React, { Component } from "react";
import { Tabs, Tab } from "react-bootstrap";
import NewPurchaseHeader from "./NewPurchaseHeader";
import PurchaseDetailsByItem from "./PurchaseDetailsByItem";
import NewPurchaseReview from "./NewPurchaseReview";

class NewPurchaseOrderContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      key: 1,
      showByItem: true,
      showByOrder: false
    };
    this.handleSelect = this.handleSelect.bind(this);
    this.OnShowClick = this.OnShowClick.bind(this);
  }
  handleSelect(key) {
    this.setState({ key });
  }
  OnShowClick = () => {
    this.setState({
      showByItem: false,
      showByOrder: true
    });
  };
  render() {
    const { showByItem } = this.state;
    const { showByOrder } = this.state;
    return (
      <div>
        <Tabs
          className="customtabs"
          activeKey={this.state.key}
          onSelect={this.handleSelect}
          id="controlled-tab-example"
        >
          <Tab eventKey={1} title="1. Purchase Header">
            <NewPurchaseHeader />
            <section className="button-area">
              <div className="container text-center">
                <div className="BottomButtons">
                  <button
                    onClick={() => this.handleSelect("2")}
                    type="button"
                    className="btn to-button-default"
                  >
                    By Item
                  </button>
                  <button
                    onClick={() => {
                      this.handleSelect("2");
                      this.OnShowClick();
                    }}
                    type="button"
                    className="btn to-button-default"
                  >
                    By Order
                  </button>
                </div>
              </div>
            </section>
          </Tab>
          <Tab eventKey={2} title="2. Purchase Details" disabled>
            {showByItem ? <PurchaseDetailsByItem /> : null}
            {showByOrder ? (
              <div className="container-fluid ">
                <form className="es_purchase-input">
                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" for="inputUnit">
                        # Unit/Loads
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputUnit"
                        placeholder="25"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" for="inputFOB">
                        FOB
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputFOB"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" for="inputSurcharge">
                        Surcharge
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputSurcharge"
                        placeholder="$50.00"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" for="inputCreator">
                        Ship Date
                      </label>
                      <input
                        type="date"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="05/10/2019"
                      />
                    </div>
                  </div>
                </form>

                <section className="button-area">
                  <div className="container text-center">
                    <div className="BottomButtons">
                      <button
                        onClick={() => this.handleSelect("1")}
                        type="button"
                        className="btn to-button-default"
                      >
                        Go Back
                      </button>
                      <button
                        onClick={() => this.handleSelect("3")}
                        type="button"
                        className="btn to-button-default"
                      >
                        Review Order
                      </button>
                    </div>
                  </div>
                </section>
              </div>
            ) : null}
          </Tab>
          <Tab eventKey={3} title="3. Purchase Review" disabled>
            <NewPurchaseReview />
          </Tab>
        </Tabs>
      </div>
    );
  }
}

export default NewPurchaseOrderContent;
